<template>
  <div class="content">
    <van-nav-bar
      title="工单详情"
      left-text="返回"
      right-text=""
      left-arrow
      @click-left="goBack"
    />
    <div class="pay_way_group">
      <div class="pay_way_title">工单简介</div>
      <van-cell-group class="payment_group">
        <van-cell title="工单类型">{{type}}</van-cell>
        <van-cell title="工单标题">{{title}}</van-cell>
        <van-cell title="工单内容">
          {{content}}
        </van-cell>
        <van-cell title="处理进度">{{speed}} %</van-cell>
        <van-cell title="提交时间">{{create_time}}</van-cell>
        <van-cell title="受理时间">{{accept_time}}</van-cell>
        <van-cell title="受理人">{{accept_name}}</van-cell>
      </van-cell-group>
    </div>
    <van-button class="pay_submit" type="warning" @click="goBack" :bottom-action="true" style="margin-top:10px;">返回上级
    </van-button>
  </div>
</template>

<script>
  import {
    Row,
    Col,
    CellGroup,
    Cell,
    Button,
    Dialog,
    ImagePreview,
    Toast,
    NavBar,
    Field
  } from "vant";
  import { WORK_VIEW} from "@/api/user";

  export default {
    name: "workViews",
    data() {
      return {
        isSubmit : false,
        type:"",
        title:"",
        content:"",
        status:"",
        speed:"",
        create_time:"",
        accept_time:"",
        accept_name:"",
      };
    },
    props: {
      id: this.id
    },
    watch:{
			'$route': 'changeRouter'
		},
    created(){
      this.initPage();
    },

    methods: {
      initPage(){
        if(!this.id){
          return;
        }
        this.$reqGet(WORK_VIEW,{id:this.id}).then((res) => {
          this.getSuccess(res);
        }).catch((err) => {
          this.getSuccess(err);
        });
      },
      getSuccess(res){
        let result = res.data;
        if(!result || result.code != 0){
          let msg = result.msg ? result.msg : "获取工单失败";
          Toast.fail(msg);
          return;
        }
        let data = result.data;
        this.type = data.type;
        this.title = data.title;
        this.content = data.content;
        this.status = data.status;
        this.speed = data.speed;
        this.create_time = data.create_time;
        this.accept_time = data.accept_time;
        this.accept_name = data.accept_name;
      },
      submit() {
        let formdata = new FormData();
        formdata.append("id", this.id);
        formdata.append("down_price", this.down_price);
        formdata.append("tb_user", this.tb_user);
        formdata.append("tb_order_sn", this.tb_order_sn);
        formdata.append("remark", this.remark);
        this.isSubmit=true;
        this.$reqPost(UPDATE_ORDER, formdata).then((res) => {
          this.submitSuccess(res);
        }).catch((err) => {

        });
      },
      submitSuccess(res) {
        let result = res.data;
        if(result.code!=0){
          this.isSubmit=false;
          Toast(result.msg);
          return;
        }
        Toast.success(result.msg);
        setTimeout(() => {
          this.isSubmit=false;
          this.$router.go(-1)
        }, 1000)
      },
      //返回
      goBack() {
        this.$router.go(-1)
      },

      showImagePreview(img){
        ImagePreview([img]);
      },

    },

    components: {
      [Row.name]: Row,
      [Col.name]: Col,
      [CellGroup.name]: CellGroup,
      [Cell.name]: Cell,
      [Button.name]: Button,
      [ImagePreview.name]: ImagePreview,
      [Toast.name]: Toast,
      [NavBar.name]:NavBar,
      [Field.name]:Field
    },

    filters: {
      money: function(val) {
        val = val.toString().replace(/\$|\,/g,'');
        if(isNaN(val)) {
          val = "0";
        }
        let sign = (val == (val = Math.abs(val)));
        val = Math.floor(val*100+0.50000000001);
        let cents = val%100;
        val = Math.floor(val/100).toString();
        if(cents<10) {
          cents = "0" + cents
        }
        for (var i = 0; i < Math.floor((val.length-(1+i))/3); i++) {
          val = val.substring(0,val.length-(4*i+3))+',' + val.substring(val.length-(4*i+3));
        }
        return (((sign)?'':'-') + val + '.' + cents);
      }
    }
  };

</script>

<style lang="scss" scoped>

  // .payment_group {
  //   /*padding: 10px 15px;*/
  // }

  .task-countdown {
    text-align: center;
    font-size: 12px;
    background-color: #fffeec;
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    line-height: 30px;
    z-index: 999;
  }

  .pay_submit {
    /*position: fixed;*/
    /*bottom: 0;*/
    width: 100%;
  }

  .cannel_submit{
    margin-top:10px;
    width: 100%;
  }

  .pay_way_group{
    background-color: #ffffff;
    margin-bottom: 10px;
  }

  .pay_way_group img {
    vertical-align: middle;
  }

  .pay_way_title {
    font-weight: bold;
    padding: 15px;
    background-color: #fff;
  }

  .upload_hint{
    position: relative;
    padding-left: 20px;
    line-height: 1.6;
    margin: 10px;
    i{
      position: absolute;
      top: 2px;
      left: 0;
    }
  }

  .vacancy_row{
    .van-col {
      font-size: 13px;
      line-height: 30px;
      text-align: center;
      vertical-align: middle;
      margin-bottom: 0px;
      background-clip: content-box;
    }
  }

  .countdown{
    text-align: center;
    width: 100%;
    height: 50px;
    line-height: 50px;
    border: 0;
    border-radius: 0;
    font-size: 16px;
    color: #fff;
    background-color: #29a0f6;
  }

  .van-cell__right-icon {
    top: 3px;
  }

  .text_style {
    color: #29a0f6;
    font-size: 12px;
    padding-left: 15px;
    text-align: left;
    margin: 0;
    padding-bottom: 7px;
  }
  .text_red{
    color:red;
    font-size: 12px;
    padding-left: 15px;
    text-align: left;
    margin: 0;
    padding-bottom: 7px;
  }
  .no-longtap{
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
  }
</style>



